.static-content-page {

    @include at-media("desktop") {
        .content-column {
            flex-direction: row-reverse;
            min-height: 40vh;
        }
    }

    a {
        color: color('indigo-warm-60v');

        &:hover {
            color: color('indigo-warm-70v');
        }
    }

    h2,
    h3,
    h4 {
        line-height: lh('heading', 6);
    }

    h3,
    h4 {
        font-weight: 500;
    }

    h2 {
        font-size: size('body', 12);
        font-weight: 600;

        &:first-child {
            margin-top: units(1.5);
        }
    }

    h3 {
        font-size: size('body', 11);
        color: color('indigo-warm-70v');
        margin-top: units(5);
    }

    h4 {
        font-size: size('body', 9);
        color: color('indigo-warm-80v');
    }

    p {
        font-size: size('body', 6);
        line-height: lh('body', 6);
        max-width: 100%;
        margin-top: units(1.5);

        &.usa-intro {
            margin-top: units(-0.5);
            font-size: size('body', 12);
            font-weight: 600;
            line-height: lh('body', 4);

            &.introBackground {
                font-weight: 400;
                padding: units(2) units(3);
                background: color('cyan-5v');
                margin-top: units(1);
            }
        }

        &.back-to-news-wrapper {
            margin: units(4) 0 units(2);
        }
    }

    &.blog-page {
        p {
            &.usa-intro {
                margin-top: units(1.5);
            }
        }
    }

    hr {
        margin: units(5) 0;
        max-width: 132px;
        border: none;
        border-top: 7px solid color("indigo-warm-60v");
        height: 7px;
    }

    .usa-button {
        background: color("indigo-warm-60v");
        border-color: color('indigo-warm-70v');
        font-weight: 600;
        margin-top: units(4);

        &::before,
        &::after {
            position: absolute;
            top: 0;
            left: 0;
            border-color: color('indigo-warm-70v');
            border-style: solid;
            transition: all 0.35s;
            content: "";
            z-index: 1;
        }

        &::before {
            width: 0;
            height: 100%;
            border-width: 1px 0 1px 0;
        }

        &::after {
            width: 100%;
            height: 0;
            border-width: 0 1px 0 1px;
        }

        &:hover {
            border-color: color('indigo-warm-70v');
            background-color: color('indigo-warm-60v');
            transition: background-color .6s ease;

            &::before {
                width: 100%;
                border-width: 1px 0 1px 0;
            }

            &::after {
                height: 100%;
                border-width: 0 1px 0 1px;
            }
        }

        a,
        a:active,
        a:visited {
            color: white;
            text-decoration: none;
            position: relative;
            z-index: 5;
        }
    }

    h3[tabindex]:focus {
        outline: none;
        padding: 0;
    }
}

.usa-prose>*+* {
    margin-top: units(3);
}

iframe:focus,
[href]:focus,
[tabindex]:focus,
[contentEditable=true]:focus {
    outline: units(1px) auto color('indigo-warm-60v');
    outline-offset: 0.3rem;
}

.return-to-top {
    transition: all .5s ease-in-out;
    font-size: size('body', 5);
    margin: 0 0 units(2.5) 0 !important;

    &.fixed {
        position: fixed;
        bottom: 30px;
        transition: all .5s ease-in-out;
        z-index: 1000;
    }
}

.sampleText {
